@import '../fonts/stylesheet.css';
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: "HelveticaNeueCyr";
  font-weight: 400;
  font-size: 18px;
  background-color: #000;
  color: #fff;
}

input {
  border: none;
  outline: none;
  font-size: 20px;
  background-color: transparent;
}

html,
body {
  overflow-x: hidden;
}

.header {
  background-color: #fff;
  margin: 5px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__roll {
  font-size: 80px;
  color: #000;
}
.header__timeline {
  color: #000;
  font-size: 80px;
}

.head {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
  border-bottom: 3px solid #fff;
  position: relative;
  overflow: hidden;
}
.head__container {
  font-size: 80px;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  flex-wrap: wrap;
  max-width: 1366px;
  width: 100%;
  height: 100%;
}
.head__title {
  font-size: 157px;
  font-weight: 400;
  text-transform: uppercase;
  width: 100%;
}
@media (max-width: 1600px) {
  .head__title {
    font-size: 120px;
  }
}
.head__wrapper {
  max-width: 565px;
  display: flex;
  flex-direction: column;
  width: 100%;
  font-size: 157px;
}
.head__date {
  line-height: 100%;
  text-align: right;
  margin-top: -25px;
}

.events {
  display: flex;
}
.events__container {
  width: 100%;
}
.events__container:first-child {
  max-width: 40%;
  width: 100%;
  border-right: 3px solid #fff;
  padding: 20px;
}
.events__container:last-child {
  max-width: 60%;
  width: 100%;
}
.events__title {
  font-size: 80px;
  font-weight: 300;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}
@media (max-width: 1600px) {
  .events__title {
    font-size: 60px;
  }
}
.events__info-block {
  position: relative;
  display: flex;
  transition: all 0.6s;
}
.events__info-text {
  pointer-events: none;
  font-size: 157px;
}
.events__info-image {
  position: absolute;
  right: 0;
  top: 0;
  pointer-events: none;
  transition: 0.5s ease;
}
.events__info-wrapper {
  position: relative;
  cursor: pointer;
}
.events__info-wrapper-1 {
  margin-top: 80px;
}
.events__info-wrapper:hover .events__info-visible {
  opacity: 0;
}
.events__info-wrapper:hover .events__info-hidden {
  opacity: 1;
}
.events__info-visible {
  transition: all 0.6s;
}
.events__info-hidden {
  transition: all 0.6s;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: 100%;
  opacity: 0;
  pointer-events: none;
}
.events__info-visible-title {
  font-size: 157px;
  font-weight: 400;
  line-height: 100%;
}
.events__info-visible-subtitle {
  font-size: 80px;
  font-weight: 300;
  line-height: 100%;
  margin-top: -15px;
}
.events__info-1 .events__info-image {
  max-width: 80%;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
}
.events__info-2 {
  justify-content: flex-end;
}
.events__info-2 .events__info-image {
  bottom: -40px;
  top: 100px;
}
.events__info-3 .events__info-text {
  overflow: hidden;
}
.events__info-3 .events__info-text:first-child {
  overflow: initial;
}
.events__info-3 .events__info-text:nth-child(2) {
  display: flex;
  justify-content: flex-end;
  width: 130px;
}
.events__info-3 .events__info-text:nth-child(3) {
  display: flex;
  justify-content: flex-end;
  width: 45px;
  margin-left: 60px;
}
.events__info-3 .events__info-text:nth-child(4) {
  display: flex;
  justify-content: flex-end;
  width: 36px;
  margin-left: 90px;
}
.events__info-4 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.events__info-4 .events__info-text {
  font-size: 100px;
}
.events__info-4 .events__info-wrapper:first-child {
  margin-top: 80px;
}
.events__info-4 .events__info-wrapper:first-child .events__info-text:nth-child(1) {
  height: 36px;
  overflow: hidden;
}
.events__info-4 .events__info-wrapper:first-child .events__info-text:nth-child(2) {
  height: 52px;
  overflow: hidden;
}
.events__info-4 .events__info-wrapper:last-child .events__info-text:nth-child(3) {
  height: 42px;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
}
.events__info-4 .events__info-wrapper:last-child .events__info-text:nth-child(2) {
  height: 52px;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
}
.events__info-5 {
  justify-content: flex-end;
}
.events__info-5 .events__info-image {
  top: 0%;
}
.events__info-5 .events__info-text {
  line-height: 100%;
}
.events__info-wrapper-1 {
  margin-top: 60px;
}
.events__info-wrapper-2 {
  justify-content: flex-end;
  margin: 15px 0;
}
.events__info-wrapper-2 .events__info-visible {
  width: 100%;
}
.events__info-wrapper-2 .events__info-visible-title {
  text-align: right;
}
.events__info-wrapper-2 .events__info-visible-subtitle {
  text-align: right;
  margin-right: 15%;
}
.events__info-wrapper-2 .events__info-visible-subtitle:last-child {
  margin-right: 0;
}
.events__info-wrapper-3 {
  margin-top: 10px;
  margin-bottom: 28px;
}
.events__info-wrapper-4 {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 28px;
}
.events__info-wrapper-4 .events__info-visible {
  width: 100%;
}
.events__info-wrapper-4 .events__info-visible--wrapper:first-child {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.events__info-wrapper-4 .events__info-visible--wrapper:last-child .events__info-visible-subtitle:last-child {
  margin-left: 25%;
}
.events__info-wrapper-4 .events__info-wrapper {
  flex-direction: column;
}
.events__info-wrapper-5 {
  justify-content: flex-end;
}
.events__info-wrapper-5 .events__info-visible {
  width: 100%;
}
.events__info-wrapper-5 .events__info-visible-title,
.events__info-wrapper-5 .events__info-visible-subtitle:last-child {
  text-align: right;
}
.events__info-wrapper {
  display: flex;
}
.events__banner {
  background-color: #fff;
  color: #000;
  position: relative;
  min-height: 557px;
  height: 100%;
  max-height: 557px;
  padding: 80px;
}
.events__banner-wrapper {
  font-size: 80px;
  font-weight: 300;
  letter-spacing: 0.03em;
  position: absolute;
  top: 0;
}
.events__banner-wrapper:nth-child(1) {
  right: 0px;
  bottom: 0px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.events__banner-wrapper:nth-child(1) .events__banner-text:first-child {
  transform: rotate(90deg);
  transform-origin: left top 0;
  position: absolute;
  right: -100%;
  top: 21%;
}
.events__banner-wrapper:nth-child(1) .events__banner-text:nth-child(2) {
  margin-right: 11px;
}
.events__banner-wrapper:nth-child(3) {
  left: 0px;
  top: 0px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.events__banner-wrapper:nth-child(3) .events__banner-text:first-child {
  transform: rotate(90deg);
  transform-origin: left top 0;
  position: absolute;
  right: -20%;
  width: 100%;
  top: 26%;
}
.events__banner-wrapper:nth-child(3) .events__banner-text:nth-child(2) {
  margin-left: 80px;
  line-height: 100%;
}
.events__banner-container {
  position: relative;
  height: 100%;
}
.events__banner-container p {
  line-height: 85%;
}
.events__banner-container--support {
  display: inline-block;
  position: absolute;
}
.events__banner-date {
  font-size: 157px;
}
.events__banner-time {
  font-size: 157px;
}
.events__premiere-container {
  display: flex;
  overflow: hidden;
  width: 100%;
}
.events__premiere-container:first-child, .events__premiere-container:nth-child(2) {
  height: 122px;
}
.events__premiere-container:nth-child(3) {
  height: 174px;
}
.events__premiere-container:nth-child(4) {
  height: 231px;
}
.events__premiere-container:nth-child(5) {
  height: 220px;
}
.events__premiere-container:nth-child(6) {
  height: 270px;
}
.events__premiere-container:nth-child(7) {
  height: 348px;
}
.events__premiere-container > div {
  width: 50%;
}
.events__premiere-date p {
  line-height: 100%;
}
.events__premiere-date p:first-child {
  height: 48px;
  overflow: hidden;
}
.events__premiere-time p {
  line-height: 100%;
}
.events__premiere-time p:first-child {
  height: 48px;
  overflow: hidden;
}
.events__premiere-name p {
  line-height: 100%;
  overflow: hidden;
}
.events__premiere-name p:nth-child(1) {
  height: 58px;
}
.events__premiere-name p:nth-child(2) {
  height: 96px;
}
.events__premiere-name, .events__premiere-date, .events__premiere-time {
  background-color: #fff;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #000;
  font-size: 157px;
}

.footer {
  display: flex;
  justify-content: space-between;
  padding: 25px;
}
.footer__link {
  font-size: 36px;
  color: #fff;
  text-decoration: none;
}

.wrapper-animate--hidden {
  opacity: 0;
}

.wrapper-animate--visible {
  opacity: 1;
}

@media (max-width: 1600px) {
  .events__banner-date,
.events__banner-time {
    font-size: 120px;
  }
}
@media (max-width: 1366px) {
  .head {
    padding: 30px;
  }

  .events__info-visible-title {
    font-size: 120px;
  }
  .events__info-text {
    font-size: 120px;
  }
  .events__premiere-name, .events__premiere-date, .events__premiere-time {
    font-size: 110px;
  }
}
@media (max-width: 1269px) {
  .events__title {
    font-size: 44px;
  }
  .events__info-visible-title {
    font-size: 80px;
  }
  .events__info-text {
    font-size: 80px;
  }
  .events__info-visible-subtitle {
    font-size: 50px;
  }
  .events .events__info-2 .events__info-image {
    top: 40px;
  }
  .events__info-wrapper-4 .events__info-text {
    font-size: 70px;
  }
  .events__premiere-name, .events__premiere-date, .events__premiere-time {
    font-size: 80px;
  }
  .events__premiere-name p {
    line-height: 100%;
    overflow: hidden;
  }
  .events__premiere-name p:nth-child(1) {
    height: 38px;
  }
  .events__premiere-name p:nth-child(2) {
    height: 48px;
  }
}
@media (max-width: 1200px) {
  .events__banner-date, .events__banner-time {
    font-size: 90px;
  }
}
@media (max-width: 768px) {
  .footer {
    background-color: #000;
  }
  .footer__link {
    font-size: 23px;
  }

  .head {
    min-height: initial;
    height: 90vh;
  }
  .head__wrapper {
    font-size: 70px;
    align-items: center;
  }
  .head__date {
    order: 0;
    margin-top: initial;
    text-align: left;
  }
  .head__time {
    order: 1;
    margin-left: 160px;
  }
  .head__container {
    height: 70%;
    justify-content: space-between;
  }

  .events {
    flex-direction: column;
  }
  .events__container:first-child {
    order: 1;
    max-width: 100%;
  }
  .events__container:last-child {
    order: 0;
    max-width: 100%;
  }
  .events__banner-date, .events__banner-time {
    font-size: 80px;
  }
  .events__banner {
    height: 557px;
  }
  .events__banner-date,
.events__banner-time {
    font-size: 70px;
  }

  .events__banner-text {
    font-size: 50px;
  }

  .events__banner-wrapper:nth-child(3) .events__banner-text:nth-child(2) {
    margin-left: 50px;
  }

  .events__banner-wrapper:nth-child(1) .events__banner-text:first-child {
    right: -98%;
    top: 48%;
  }
}
@media (max-width: 480px) {
  .header__timeline {
    font-size: 30px;
  }

  .events__banner {
    height: 400px;
    min-height: initial;
  }
  .events__title {
    font-size: 30px;
  }
  .events__premiere-name, .events__premiere-date, .events__premiere-time {
    font-size: 50px;
  }
  .events__premiere-name p {
    line-height: 100%;
    overflow: hidden;
  }
  .events__premiere-name p:nth-child(1) {
    height: 22px;
  }
  .events__premiere-name p:nth-child(2) {
    height: 30px;
  }
  .events__premiere-date p:first-child {
    height: 24px;
  }
  .events__premiere-time p:first-child {
    height: 24px;
  }
  .events__info-visible-title {
    font-size: 70px;
  }
  .events__info-text {
    font-size: 70px;
  }
  .events__premiere-container:nth-child(4) {
    height: 110px;
  }
  .events__premiere-container:nth-child(6) {
    height: 110px;
  }
  .events__premiere-container:nth-child(7) {
    height: 110px;
  }
  .events__info-wrapper-4 .events__info-text {
    font-size: 50px;
  }

  .events__banner-wrapper:nth-child(1) .events__banner-text:first-child {
    top: 28%;
  }

  .footer__link {
    font-size: 13px;
  }
}
@media (max-width: 414px) {
  .events__banner-date, .events__banner-text, .events__banner-time {
    font-size: 40px;
  }

  .events__banner-wrapper:nth-child(1) .events__banner-text:first-child {
    top: 42%;
    right: -95%;
  }

  .head__title {
    font-size: 70px;
  }
  .head__date, .head__time {
    font-size: 60px;
  }
}