@import './colors.scss';
@import './mixins.scss';

@media (max-width: 1600px) {

    .events__banner-date,
    .events__banner-time {
        font-size: 120px;
    }
}

@media (max-width: 1384px) {}

@media (max-width: 1366px) {
    .head {
        padding: 30px;
    }
    .events {
        &__info-visible-title {
            font-size: 120px;
        }

        &__info-text {
            font-size: 120px;
        }

        &__premiere-name,
        &__premiere-date,
        &__premiere-time {

            font-size: 110px;
        }
    }
}

@media (max-width: 1269px) {
    .events {

        &__title {
            font-size: 44px;
        }

        &__info-visible-title {
            font-size: 80px;
        }

        &__info-text {
            font-size: 80px;
        }

        &__info-visible-subtitle {
            font-size: 50px;
        }

        .events__info-2 .events__info-image {
            top: 40px;
        }

        &__info-wrapper-4 {

            .events__info-text {
                font-size: 70px;
            }
        }

        &__premiere-name,
        &__premiere-date,
        &__premiere-time {
            font-size: 80px;
        }

        &__premiere-name {

            p {
                line-height: 100%;
                overflow: hidden;
    
                &:nth-child(1) {
                    height: 38px;
                }
    
                &:nth-child(2) {
                    height: 48px;
                }
    
                &:nth-child(3) {}
            }
        }
    }
}

@media (max-width: 1200px) {
    .events {

        &__banner {

            &-date, &-time {
                font-size: 90px;
            }

        }
    }
}

@media (max-width: 1024px) {}

@media (max-width: 1023px) {}

@media (max-width: 768px) {
    .footer {
        
        background-color: $black;
        &__link {
            font-size: 23px;
        }
    }
    .head {

        min-height: initial;
        height: 90vh;
        &__wrapper {
            font-size: 70px;
            align-items: center;
        }
        &__date {
            order: 0;
            margin-top: initial;
            text-align: left;
        }
        &__time {
            order: 1;
            margin-left: 160px;
        }
        &__container {
            height: 70%;
            justify-content: space-between;
        }
    }
    .events {
        flex-direction: column;

        &__container {
            &:first-child {
                order: 1;
                max-width: 100%;
            }
            &:last-child {
                order: 0;
                max-width: 100%;
            }
        }
        &__banner-date, &__banner-time {
            font-size: 80px;
        }
        &__banner {
            height: 557px;
        }
        &__info-wrapper-5 {

        }
    }
    .events__banner-date,
    .events__banner-time {
        font-size: 70px;
    }
    .events__banner-text {
        font-size: 50px;
    }
    .events__banner-wrapper:nth-child(3) .events__banner-text:nth-child(2) {
        margin-left: 50px;
    }
    .events__banner-wrapper:nth-child(1) .events__banner-text:first-child {
        right: -98%;
        top: 48%;
    }
}

@media (max-width: 767px) {}

@media (max-width: 680px) {}

@media (max-width: 680px) and (max-height: 700px) {}

@media (max-width: 480px) {
    .header {

        &__timeline {
            font-size: 30px;
        }
    }
    .events {
        &__banner {
            height: 400px;
            min-height: initial;
        }
        &__title {
            font-size: 30px;
        }
        &__premiere-name,
        &__premiere-date,
        &__premiere-time {
            font-size: 50px;
        }

        &__premiere-name {

            p {
                line-height: 100%;
                overflow: hidden;
    
                &:nth-child(1) {
                    height: 22px;
                }
    
                &:nth-child(2) {
                    height: 30px;
                }
    
                &:nth-child(3) {}
            }
        }
        &__premiere-date {

            p {

                &:first-child {
                    height: 24px;
                }
            }
        }
        &__premiere-time {

            p {

                &:first-child {
                    height: 24px;
                }
            }
        }
        &__info-visible-title {
            font-size: 70px;
        }
        &__info-text {
            font-size: 70px;
        }
        &__premiere-container {

            &:nth-child(4) {
                height: 110px;
            }
            &:nth-child(6) {
                height: 110px;
            }
            &:nth-child(7) {
                height: 110px;
            }
        }
        &__info-wrapper-4 {

            .events__info-text {
                font-size: 50px;
            }
        }
    }
    .events__banner-wrapper:nth-child(1) .events__banner-text:first-child {
        top: 28%;
    }
    .footer {

        &__link {
            font-size: 13px;
        }
    }
}

@media (max-width: 414px) {
    .events__banner-date, .events__banner-text, .events__banner-time {
        font-size: 40px;
    }
    .events__banner-wrapper:nth-child(1) .events__banner-text:first-child {
        top: 42%;
        right: -95%;
    }
    .head {


        &__title {
            font-size: 70px;
        }
        &__date, &__time {
            font-size: 60px;
        }
    }
}