// @import './fonts.scss';
@import '../fonts/stylesheet.css';
@import './mixins.scss';
@import './colors.scss';

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

img {
    max-width: 100%;
    height: auto;
}

body {
    font-family: 'HelveticaNeueCyr';
    font-weight: 400;
    font-size: 18px;
    background-color: $black;
    color: $white;
}

input {
    border: none;
    outline: none;
    font-size: 20px;
    background-color: transparent;
}

html,
body {
    overflow-x: hidden;
}

.header {

    // .header__roll
    background-color: $white;
    margin: 5px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &__roll {
        font-size: 80px;
        color: $black;
    }

    &__timeline {
        color: $black;
        font-size: 80px;
    }
}

.head {

    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 90vh;
    border-bottom: 3px solid $white;
    position: relative;
    overflow: hidden;
    // .head__container

    &__container {
        font-size: 80px;
        color: $white;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        flex-wrap: wrap;
        max-width: 1366px;
        width: 100%;
        height: 100%;
    }

    // .head__title

    &__title {
        @include h2;
        text-transform: uppercase;
        width: 100%;
    }

    // .head__wrapper

    &__wrapper {
        max-width: 565px;
        display: flex;
        flex-direction: column;
        width: 100%;
        font-size: 157px;
    }

    // .head__time

    &__time {}

    // .head__date

    &__date {
        line-height: 100%;
        text-align: right;
        margin-top: -25px;
    }
}

.events {

    display: flex;

    // .events__container

    &__container {

        width: 100%;

        &:first-child {
            max-width: 40%;
            width: 100%;
            border-right: 3px solid $white;
            padding: 20px;
        }

        &:last-child {
            max-width: 60%;
            width: 100%;
        }
    }

    // .events__title

    &__title {
        @include h3;
        text-transform: uppercase;
    }

    // .events__info

    &__info {}

    // .events__info-block

    &__info-block {
        position: relative;
        display: flex;
        transition: all 0.6s;
    }

    // .events__info-text

    &__info-text {
        pointer-events: none;
        font-size: 157px;
    }

    // .events__info-image

    &__info-image {
        position: absolute;
        right: 0;
        top: 0;
        pointer-events: none;
        transition: 0.5s ease;

    }

    &__info-wrapper {
        position: relative;

        &-1 {
            margin-top: 80px;
        }

        cursor: pointer;

        &:hover {

            .events__info-visible {
                opacity: 0;
            }

            .events__info-hidden {
                opacity: 1;
            }
        }
    }

    // .events__info-visible

    &__info-visible {
        transition: all 0.6s;
    }

    // .events__info-hidden

    &__info-hidden {
        transition: all 0.6s;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        width: 100%;
        opacity: 0;
        pointer-events: none;
    }

    // .events__info-visible-title

    &__info-visible-title {
        font-size: 157px;
        font-weight: 400;
        line-height: 100%;
    }

    // .events__info-visible-subtitle

    &__info-visible-subtitle {
        font-size: 80px;
        font-weight: 300;
        line-height: 100%;
        margin-top: -15px;
    }

    // .events__info-1

    &__info-1 {

        .events__info-image {
            max-width: 80%;
            top: 50%;
            right: 30px;
            transform: translateY(-50%);
        }
    }

    // .events__info-2

    &__info-2 {
        justify-content: flex-end;

        .events__info-image {
            bottom: -40px;
            top: 100px;
        }
    }

    // .events__info-3

    &__info-3 {

        .events__info-text {
            overflow: hidden;

            &:first-child {
                overflow: initial;
            }
        }

        .events__info-text:nth-child(1) {}

        .events__info-text:nth-child(2) {
            display: flex;
            justify-content: flex-end;
            width: 130px;
        }

        .events__info-text:nth-child(3) {
            display: flex;
            justify-content: flex-end;
            width: 45px;
            margin-left: 60px;
        }

        .events__info-text:nth-child(4) {
            display: flex;
            justify-content: flex-end;
            width: 36px;
            margin-left: 90px;
        }
    }

    // .events__info-4

    &__info-4 {
        display: flex;
        align-items: center;
        justify-content: center;

        .events__info-text {
            font-size: 100px;
        }

        .events__info-wrapper:first-child {
            margin-top: 80px;

            .events__info-text:nth-child(1) {
                height: 36px;
                overflow: hidden;
            }

            .events__info-text:nth-child(2) {
                height: 52px;
                overflow: hidden;
            }

            .events__info-text:nth-child(3) {}
        }

        .events__info-wrapper:last-child {
            .events__info-text:nth-child(3) {
                height: 42px;
                overflow: hidden;
                display: flex;
                align-items: flex-end;
            }

            .events__info-text:nth-child(2) {
                height: 52px;
                overflow: hidden;
                display: flex;
                align-items: flex-end;
            }

            .events__info-text:nth-child(1) {}
        }
    }

    // .events__info-5

    &__info-5 {
        justify-content: flex-end;

        .events__info-image {
            top: 0%;
        }

        .events__info-text {
            line-height: 100%;
        }
    }

    // .events__info-wrapper-1

    &__info-wrapper-1 {
        margin-top: 60px;
    }

    // .events__info-wrapper-1

    &__info-wrapper-2 {
        justify-content: flex-end;
        margin: 15px 0;

        .events__info-visible {
            width: 100%;
        }

        .events__info-visible-title {
            text-align: right;
        }

        .events__info-visible-subtitle {
            text-align: right;
            margin-right: 15%;
            &:last-child {
                margin-right: 0;
            }
        }
    }

    // .events__info-wrapper-1

    &__info-wrapper-3 {
        margin-top: 10px;
        margin-bottom: 28px;
    }

    // .events__info-wrapper-1

    &__info-wrapper-4 {
        width: 100%;
        margin-top: 10px;
        margin-bottom: 28px;

        .events__info-visible {
            width: 100%;
        }

        .events__info-visible--wrapper:first-child {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
        }

        .events__info-visible--wrapper:last-child {
            .events__info-visible-subtitle:last-child {
                margin-left: 25%;
            }
        }

        .events__info-wrapper {
            flex-direction: column;
        }
    }

    // .events__info-wrapper-1

    &__info-wrapper-5 {
        justify-content: flex-end;

        .events__info-visible {
            width: 100%;
        }

        .events__info-visible-title,
        .events__info-visible-subtitle:last-child {
            text-align: right;
        }
    }

    // .events__info-wrapper

    &__info-wrapper {
        display: flex;
    }

    // .events__banner

    &__banner {
        background-color: $white;
        color: $black;
        position: relative;
        min-height: 557px;
        height: 100%;
        max-height: 557px;
        padding: 80px;
    }

    // .events__banner-wrapper

    &__banner-wrapper {
        font-size: 80px;
        font-weight: 300;
        letter-spacing: 0.03em;
        position: absolute;
        top: 0;

        &:nth-child(1) {
            right: 0px;
            bottom: 0px;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;

            .events__banner-text:first-child {
                transform: rotate(90deg);
                transform-origin: left top 0;
                position: absolute;
                right: -100%;
                top: 21%;
            }

            .events__banner-text:nth-child(2) {
                margin-right: 11px;
            }
        }

        &:nth-child(3) {
            left: 0px;
            top: 0px;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;

            .events__banner-text:first-child {
                transform: rotate(90deg);
                transform-origin: left top 0;
                position: absolute;
                right: -20%;
                width: 100%;
                top: 26%;
            }

            .events__banner-text:nth-child(2) {
                margin-left: 80px;
                line-height: 100%;
            }
        }
    }

    // .events__banner-text

    &__banner-text {}

    // .events__banner-container

    &__banner-container {

        position: relative;
        height: 100%;

        p {
            line-height: 85%;
        }
    }

    // .events__banner-container--support

    &__banner-container--support {
        display: inline-block;
        position: absolute;
    }

    // .events__banner-date

    &__banner-date {
        font-size: 157px;
    }

    // .events__banner-time

    &__banner-time {
        font-size: 157px;
    }

    // .events__premiere

    &__premiere {}

    // .events__premiere-container

    &__premiere-container {
        display: flex;
        overflow: hidden;
        width: 100%;

        &:first-child,
        &:nth-child(2) {
            height: 122px;
        }

        &:nth-child(3) {
            height: 174px;
        }

        &:nth-child(4) {
            height: 231px;
        }

        &:nth-child(5) {
            height: 220px;
        }

        &:nth-child(6) {
            height: 270px;
        }

        &:nth-child(7) {
            height: 348px;
        }

        >div {
            width: 50%;
        }
    }

    // .events__premiere-image

    &__premiere-image {}

    // .events__premiere-date

    &__premiere-date {

        p {
            line-height: 100%;

            &:first-child {
                height: 48px;
                overflow: hidden;
            }

            &:last-child {}
        }
    }

    // .events__premiere-time

    &__premiere-time {
        p {
            line-height: 100%;

            &:first-child {
                height: 48px;
                overflow: hidden;
            }

            &:last-child {}
        }
    }

    // .events__premiere-name

    &__premiere-name {

        p {
            line-height: 100%;
            overflow: hidden;

            &:nth-child(1) {
                height: 58px;
            }

            &:nth-child(2) {
                height: 96px;
            }

            &:nth-child(3) {}
        }
    }

    &__premiere-name,
    &__premiere-date,
    &__premiere-time {
        background-color: $white;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        color: $black;
        font-size: 157px;
    }
}

.footer {

    display: flex;
    justify-content: space-between;
    padding: 25px;
    // .footer__link

    &__link {
        font-size: 36px;
        color: $white;
        text-decoration: none;
    }
}

.svg--hidden {}

.banner-animate--support {}

.wrapper-animate--hidden {
    opacity: 0;
}

.wrapper-animate--visible {
    opacity: 1;
}

























